'use client';

import { Post } from '@/types';
import { PostCard } from './PostCard';

interface PostFeedProps {
  posts: Post[];
  onPostUpdated: (post: Post) => void;
  showActions?: boolean;
}

export function PostFeed({ posts, onPostUpdated, showActions = true }: PostFeedProps) {
  if (posts.length === 0) {
    return (
      <div className="text-center py-12 space-y-4">
        <h3 className="text-lg font-medium">Nenhum post encontrado</h3>
        <p className="text-muted-foreground">
          {showActions 
            ? 'Seja o primeiro a compartilhar algo interessante!' 
            : 'Faça login para ver mais posts e interagir com a comunidade.'
          }
        </p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {posts.map((post) => (
        <PostCard
          key={post._id?.toString()}
          post={post}
          onPostUpdated={onPostUpdated}
          showActions={showActions}
        />
      ))}
    </div>
  );
}
