'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/components/providers/AuthProvider';
import { CreatePost } from '@/components/posts/CreatePost';
import { PostFeed } from '@/components/posts/PostFeed';
import { Post } from '@/types';
import { Button } from '@/components/ui/button';
import Link from 'next/link';

export default function HomePage() {
  const { user, isLoading } = useAuth();
  const [posts, setPosts] = useState<Post[]>([]);
  const [loadingPosts, setLoadingPosts] = useState(true);

  useEffect(() => {
    fetchPosts();
  }, []);

  const fetchPosts = async () => {
    try {
      const response = await fetch('/api/posts');
      if (response.ok) {
        const data = await response.json();
        setPosts(data.data || []);
      }
    } catch (error) {
      console.error('Error fetching posts:', error);
    } finally {
      setLoadingPosts(false);
    }
  };

  const handlePostCreated = (newPost: Post) => {
    setPosts([newPost, ...posts]);
  };

  const handlePostUpdated = (updatedPost: Post) => {
    setPosts(posts.map(post => 
      post._id === updatedPost._id ? updatedPost : post
    ));
  };

  if (isLoading) {
    return (
      <div className="max-w-2xl mx-auto space-y-6">
        <div className="h-32 bg-muted animate-pulse rounded-lg"></div>
        <div className="space-y-4">
          {[...Array(3)].map((_, i) => (
            <div key={i} className="h-48 bg-muted animate-pulse rounded-lg"></div>
          ))}
        </div>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="max-w-2xl mx-auto text-center space-y-6">
        <div className="space-y-4">
          <h1 className="text-4xl font-bold tracking-tight">
            Bem-vindo à Social Platform
          </h1>
          <p className="text-xl text-muted-foreground">
            Conecte-se com pessoas, compartilhe momentos e descubra conteúdos incríveis.
          </p>
        </div>
        
        <div className="flex flex-col sm:flex-row gap-4 justify-center">
          <Button size="lg" asChild>
            <Link href="/register">Criar Conta</Link>
          </Button>
          <Button variant="outline" size="lg" asChild>
            <Link href="/login">Fazer Login</Link>
          </Button>
        </div>

        <div className="mt-12 space-y-8">
          <h2 className="text-2xl font-semibold">Posts Recentes</h2>
          {loadingPosts ? (
            <div className="space-y-4">
              {[...Array(3)].map((_, i) => (
                <div key={i} className="h-48 bg-muted animate-pulse rounded-lg"></div>
              ))}
            </div>
          ) : (
            <PostFeed 
              posts={posts} 
              onPostUpdated={handlePostUpdated}
              showActions={false}
            />
          )}
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-2xl mx-auto space-y-6">
      <div className="space-y-4">
        <h1 className="text-3xl font-bold">Feed</h1>
        <CreatePost onPostCreated={handlePostCreated} />
      </div>

      <div className="space-y-6">
        {loadingPosts ? (
          <div className="space-y-4">
            {[...Array(5)].map((_, i) => (
              <div key={i} className="h-48 bg-muted animate-pulse rounded-lg"></div>
            ))}
          </div>
        ) : posts.length === 0 ? (
          <div className="text-center py-12 space-y-4">
            <h3 className="text-lg font-medium">Nenhum post encontrado</h3>
            <p className="text-muted-foreground">
              Seja o primeiro a compartilhar algo interessante!
            </p>
          </div>
        ) : (
          <PostFeed 
            posts={posts} 
            onPostUpdated={handlePostUpdated}
            showActions={true}
          />
        )}
      </div>
    </div>
  );
}
