'use client';

import { useState } from 'react';
import { useAuth } from '@/components/providers/AuthProvider';
import { Post } from '@/types';
import { Card, CardContent, CardHeader } from '@/components/ui/card';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Separator } from '@/components/ui/separator';
import { toast } from 'sonner';

interface PostCardProps {
  post: Post;
  onPostUpdated: (post: Post) => void;
  showActions?: boolean;
}

export function PostCard({ post, onPostUpdated, showActions = true }: PostCardProps) {
  const { user, token } = useAuth();
  const [isLiking, setIsLiking] = useState(false);
  const [showComments, setShowComments] = useState(false);
  const [newComment, setNewComment] = useState('');
  const [isCommenting, setIsCommenting] = useState(false);

  const isLiked = user && post.likes.some(like => like.toString() === user._id?.toString());
  const canInteract = user && token && showActions;

  const handleLike = async () => {
    if (!canInteract || isLiking) return;

    setIsLiking(true);
    try {
      const response = await fetch(`/api/posts/${post._id}/like`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      const data = await response.json();
      if (response.ok && data.success) {
        onPostUpdated(data.data);
      } else {
        toast.error(data.error || 'Erro ao curtir post');
      }
    } catch (error) {
      console.error('Error liking post:', error);
      toast.error('Erro ao curtir post');
    } finally {
      setIsLiking(false);
    }
  };

  const handleComment = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!canInteract || !newComment.trim() || isCommenting) return;

    setIsCommenting(true);
    try {
      const response = await fetch(`/api/posts/${post._id}/comments`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`,
        },
        body: JSON.stringify({ content: newComment.trim() }),
      });

      const data = await response.json();
      if (response.ok && data.success) {
        onPostUpdated(data.data);
        setNewComment('');
        toast.success('Comentário adicionado!');
      } else {
        toast.error(data.error || 'Erro ao comentar');
      }
    } catch (error) {
      console.error('Error commenting:', error);
      toast.error('Erro ao comentar');
    } finally {
      setIsCommenting(false);
    }
  };

  const formatDate = (date: Date | string) => {
    const d = new Date(date);
    return d.toLocaleDateString('pt-BR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  return (
    <Card>
      <CardHeader className="pb-3">
        <div className="flex items-center space-x-3">
          <Avatar className="h-10 w-10">
            <AvatarFallback>
              {post.authorName.charAt(0).toUpperCase()}
            </AvatarFallback>
          </Avatar>
          <div className="flex-1">
            <p className="font-medium">{post.authorName}</p>
            <p className="text-sm text-muted-foreground">
              {formatDate(post.createdAt)}
            </p>
          </div>
        </div>
      </CardHeader>
      
      <CardContent className="space-y-4">
        <div className="whitespace-pre-wrap text-sm leading-relaxed">
          {post.content}
        </div>

        {showActions && (
          <>
            <div className="flex items-center space-x-4 pt-2">
              <Button
                variant="ghost"
                size="sm"
                onClick={handleLike}
                disabled={!canInteract || isLiking}
                className={isLiked ? 'text-red-600 hover:text-red-700' : ''}
              >
                {isLiked ? '❤️' : '🤍'} {post.likes.length}
              </Button>
              
              <Button
                variant="ghost"
                size="sm"
                onClick={() => setShowComments(!showComments)}
              >
                💬 {post.comments.length}
              </Button>
            </div>

            {showComments && (
              <>
                <Separator />
                <div className="space-y-4">
                  {post.comments.length > 0 && (
                    <div className="space-y-3">
                      {post.comments.map((comment, index) => (
                        <div key={index} className="flex space-x-3">
                          <Avatar className="h-8 w-8">
                            <AvatarFallback className="text-xs">
                              {comment.authorName.charAt(0).toUpperCase()}
                            </AvatarFallback>
                          </Avatar>
                          <div className="flex-1 space-y-1">
                            <div className="flex items-center space-x-2">
                              <p className="text-sm font-medium">{comment.authorName}</p>
                              <p className="text-xs text-muted-foreground">
                                {formatDate(comment.createdAt)}
                              </p>
                            </div>
                            <p className="text-sm">{comment.content}</p>
                          </div>
                        </div>
                      ))}
                    </div>
                  )}

                  {canInteract && (
                    <form onSubmit={handleComment} className="space-y-2">
                      <Textarea
                        placeholder="Escreva um comentário..."
                        value={newComment}
                        onChange={(e) => setNewComment(e.target.value)}
                        className="min-h-[80px] resize-none"
                        disabled={isCommenting}
                      />
                      <div className="flex justify-end">
                        <Button
                          type="submit"
                          size="sm"
                          disabled={!newComment.trim() || isCommenting}
                        >
                          {isCommenting ? 'Enviando...' : 'Comentar'}
                        </Button>
                      </div>
                    </form>
                  )}
                </div>
              </>
            )}
          </>
        )}
      </CardContent>
    </Card>
  );
}
